<template>
  <div class="index">
    <h1>{{msg}}</h1>
    <div class="search-box">
      <input type="text" @keyup="search" v-model.trim="song_name" placeholder="搜索歌曲名/歌手/专辑名">
      <audio v-bind:src="song_id" controls="controls" autoplay="autoplay"></audio>
    </div>
    <ul class="songs-list" v-for="song in songsList">
      <li>{{song.name}} -{{song.al.name}}- {{song.ar[0].name}}--
        <button type="button" name="button" @click="play(song.id)">点击试听</button>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
      msg: 'welcome to my music app',
      songsList:[],
      song_name:'',
      song_id:'',
    }
  },
  methods: {

    //查询歌曲列表
    search(){
      let that = this
      if(that.song_name == ''){
        that.songsList = []
        return false
      }else{

        //请求 search 接口，查询歌曲数据
        that.axios.get('https://api.imjad.cn/cloudmusic/?type=search&s='+ that.song_name).then((res) => {
          that.songsList = res.data.result.songs
        })
      }
    },

    //播放歌曲
    play(songId){
      let that = this
      that.axios.get('https://api.imjad.cn/cloudmusic/?type=song&id='+ songId+ '&br=320000').then((res) => {
        //console.log(res.data.data[0].url)
        that.song_id = res.data.data[0].url
      })
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
  text-align: left;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.search-box input{
  width: 40%;
  height: 30px;
  padding: 5px 10px;
  outline: none;
  float: left;
}
</style>



